import React, { Component } from 'react';
import {Steps,WhiteSpace} from 'antd-mobile';
import styles  from './index.less';
import NoResult from '../../../../component/noResult';
const Step = Steps.Step;
export default class reserve extends Component {
    state = {
        steps : [{
            title: '上门量尺',
            description: <div className={styles.desc}>
                            <div>{"放样员"} : <span className={styles.people}>{"陈莉莉 13065564523"}</span></div>
                            <WhiteSpace size="xs"/>
                            <div>{"预约上门时间"} : {"2018-10-12 16:30"}</div>
                            <WhiteSpace size="xs"/>
                            <div>{"完成时间"} : {"2018-10-12 16:30"}</div>
                        </div>,
          }, {
            title: '付款',
            description: <div className={styles.desc}>
            <div>{"放样员"} : <span className={styles.people}>{"陈莉莉 13065564523"}</span></div>
            <WhiteSpace size="xs"/>
            <div>{"预约上门时间"} : {"2018-10-12 16:30"}</div>
            <WhiteSpace size="xs"/>
            <div>{"完成时间"} : {"2018-10-12 16:30"}</div>
        </div>,
          }, {
            title: '方案设计',
            description: <div className={styles.desc}>
            <div>{"放样员"} : <span className={styles.people}>{"陈莉莉 13065564523"}</span></div>
            <WhiteSpace size="xs"/>
            <div>{"预约上门时间"} : {"2018-10-12 16:30"}</div>
            <WhiteSpace size="xs"/>
            <div>{"完成时间"} : {"2018-10-12 16:30"}</div>
        </div>,
          },{
            title: '上门复尺',
            description: <div className={styles.desc}>
            <div>{"放样员"} : <span className={styles.people}>{"陈莉莉 13065564523"}</span></div>
            <WhiteSpace size="xs"/>
            <div>{"预约上门时间"} : {"2018-10-12 16:30"}</div>
            <WhiteSpace size="xs"/>
            <div>{"完成时间"} : {"2018-10-12 16:30"}</div>
        </div>,
          },{
            title: '上门放样',
            description: <div className={styles.desc}>
            <div>{"放样员"} : <span className={styles.people}>{"陈莉莉 13065564523"}</span></div>
            <WhiteSpace size="xs"/>
            <div>{"预约上门时间"} : {"2018-10-12 16:30"}</div>
            <WhiteSpace size="xs"/>
            <div>{"完成时间"} : {"2018-10-12 16:30"}</div>
        </div>,
          }],
    }
    componentDidMount(){
    }
    getSteps=()=>{
        return this.state.steps.map((s, i) => <Step key={i} title={s.title} description={s.description} />);
       }
    render() {
        let {} = this.state;
        return (
            <div className={styles.schedule}>  
                <Steps current={1} direction="vertical" >
                     {this.getSteps()}
                </Steps>
            </div>
        );
    }
}